import React, {useRef, useContext, useCallback, useEffect, MouseEvent as RMouseEvent} from "react";
import {Scene, Cube} from "motor-js";

import {ConfigContext} from "../ConfigProvider";


const PathBuilder: React.FC = () => {
    const rootRef = useRef(null);
    const sceneRef = useRef<Scene>();
    const {isRunning, fov, pitch, yaw, direction} = useContext(ConfigContext);

    const handleContextMenu = useCallback((e: RMouseEvent) => {
        if(!sceneRef.current) {
            return;
        }

        const scene = sceneRef.current;
        scene.getCamera().activateDebug();

        e.preventDefault();
        e.stopPropagation();
    }, []);

    const handleMouseDown = useCallback(() => {
        if(!sceneRef.current) {
            return;
        }

        const handleMove = (e: MouseEvent) => {
            if(!sceneRef.current) {
                return;
            }

            const camera = sceneRef.current.getCamera();

            camera.setDirectionFromAngles(
                camera.getPitch() - e.movementY * 0.1,
                camera.getYaw() + e.movementX * 0.1
            );
        };

        const handleUp = () => {
            document.removeEventListener("mousemove", handleMove);
            document.removeEventListener("mouseup", handleUp);
        };

        document.addEventListener("mousemove", handleMove);
        document.addEventListener("mouseup", handleUp);
    }, []);

    useEffect(() => {
        if(!rootRef.current) {
            return;
        }

        const scene = sceneRef.current = new Scene("canvas");

        scene.add(new Cube(100, 100, 100), {x: 0, y: 0, z: -200});

        for(let i = -10; i <= 10; i++) {
            for(let j = -10; j <= 10; j++) {
                scene.add(new Cube(200, 1, 200), {x: 200 * i, y: -1000, z: 200 * j});
            }
        }

        scene.run(rootRef.current);

        return () => {
            scene.stop();
        };
    }, []);

    useEffect(() => {
        if(!rootRef.current || !sceneRef.current) {
            return;
        }

        const scene = sceneRef.current;

        if(!isRunning) {
            scene.stop();
        }
        else {
            scene.run(rootRef.current);
        }
    }, [isRunning]);

    useEffect(() => {
        if(!sceneRef.current) {
            return;
        }

        const camera = sceneRef.current.getCamera();

        camera.setFov(fov);
    }, [fov]);

    useEffect(() => {
        if(!sceneRef.current) {
            return;
        }

        const camera = sceneRef.current.getCamera();

        camera.setPitch(pitch);
    }, [pitch]);

    useEffect(() => {
        if(!sceneRef.current) {
            return;
        }

        const camera = sceneRef.current.getCamera();

        camera.setYaw(yaw);
    }, [yaw]);

    // useEffect(() => {
    //     if(!sceneRef.current) {
    //         return;
    //     }
    //
    //     const camera = sceneRef.current.getCamera();
    //
    //     camera.setDirection(direction);
    // }, [direction]);

    useEffect(() => {
        const handleDown = (e: KeyboardEvent) => {
            if(!sceneRef.current) {
                return;
            }

            const scene = sceneRef.current;
            const camera = scene.getCamera();
            let position = camera.getPosition();

            switch(e.key) {
                case "ArrowUp":
                    camera.setPosition({
                        ...position,
                        z: position.z + 1
                    });
                    break;

                case "ArrowDown":
                    camera.setPosition({
                        ...position,
                        z: position.z - 1
                    });
                    break;

                case "ArrowLeft":
                    camera.setPosition({
                        ...position,
                        x: position.x + 1
                    });
                    break;

                case "ArrowRight":
                    camera.setPosition({
                        ...position,
                        x: position.x - 1
                    });
                    break;

                case "w": {
                    const camera = scene.getCamera();

                    camera.setPosition({
                        x: camera.getPosition().x + camera.getDirection().x,
                        y: camera.getPosition().y + camera.getDirection().y,
                        z: camera.getPosition().z + camera.getDirection().z
                    });
                    // setPosition(camera.getPosition());
                    e.preventDefault();
                    e.stopPropagation();
                    break;
                }

                case "a": {
                    const camera = scene.getCamera();

                    camera.setPosition({
                        x: camera.getPosition().x + camera.getDirectionLeft().x,
                        y: camera.getPosition().y + camera.getDirectionLeft().y,
                        z: camera.getPosition().z + camera.getDirectionLeft().z
                    });
                    // setPosition(camera.getPosition());
                    e.preventDefault();
                    e.stopPropagation();
                    break;
                }

                case "d": {
                    const camera = scene.getCamera();

                    camera.setPosition({
                        x: camera.getPosition().x - camera.getDirectionLeft().x,
                        y: camera.getPosition().y - camera.getDirectionLeft().y,
                        z: camera.getPosition().z - camera.getDirectionLeft().z
                    });
                    // setPosition(camera.getPosition());
                    e.preventDefault();
                    e.stopPropagation();
                    break;
                }

                case "s": {
                    const camera = scene.getCamera();

                    camera.setPosition({
                        x: camera.getPosition().x - camera.getDirection().x,
                        y: camera.getPosition().y - camera.getDirection().y,
                        z: camera.getPosition().z - camera.getDirection().z
                    });
                    // setPosition(camera.getPosition());
                    e.preventDefault();
                    e.stopPropagation();
                    break;
                }

                case "q": {
                    e.preventDefault();
                    e.stopPropagation();
                    break;
                }

                case "e": {
                    e.preventDefault();
                    e.stopPropagation();
                    break;
                }
            }
        };

        document.addEventListener("keydown", handleDown);

        return () => {
            document.removeEventListener("keydown", handleDown);
        };
    }, []);

    return (
        <div
          ref={rootRef}
          style={{
            width: "100%",
            height: "calc(100vh - 55px)"
          }}
          onContextMenu={handleContextMenu}
          onMouseDown={handleMouseDown} />
    );
};


export {PathBuilder};
